.contacts {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        margin-top: 34px;
        padding-bottom: 84px;
        @media (max-width: 1200px) {
            margin-top: 25px;
            padding-bottom: 70px;
        }
        @media (max-width: 992px) {
            flex-direction: column;
            margin-top: 20px;
            padding-bottom: 50px;
        }
        @media (max-width: 768px) {
            margin-top: 15px;
            padding-bottom: 30px;
        }
    }

    &-info {
        width: calc(100% - 55.875%);
        padding-right: 24px;
        padding-bottom: 88px;
        padding-top: 14px;
        @media (max-width: 1200px) {
            width: calc(100% - 65%);
            padding-bottom: 70px;
        }
        @media (max-width: 992px) {
            width: 100%;
            padding-top: 0;
            padding-right: 0;
            padding-bottom: 50px;
        }
        @media(max-width: 768px) {
            padding-bottom: 40px;
        }
        .header-block {
            margin-bottom: 5rem;
            @media (max-width: 768px) {
                margin-bottom: 3rem;
            }
        }

        &__item {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            margin-top: 6.4rem;
            @media (max-width: 992px) {
                width: calc(50% - 10px);
                margin-top: 0;
                margin-right: 20px;
                margin-bottom: 6.4rem;
                &:nth-last-child(-n + 2) {
                    margin-bottom: 0;
                }
                &:nth-child(2n) {
                    margin-right: 0;
                }
            }
            @media (max-width: 768px) {
                margin-bottom: 20px;
            }
            @media(max-width: 480px) {
                width: 100%;
                margin-right: 0;
                margin-bottom: 20px;
                &:nth-last-child(-n + 2) {
                    margin-bottom: 20px;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
            &:first-child {
                margin-top: 0;
            }

            .title {
                font-size: 1.6rem;
                line-height: 1.5;
                color: $orange-dim;
            }

            a {
                @include transition();

                &:hover {
                    text-decoration: none;
                    color: $orange-dim;
                }
            }

            .value {
                display: flex;
                align-items: flex-start;
                max-width: 620px;
                font-size: 1.8rem;
                line-height: 1.33;
                color: $white;
                margin-top: 8px;

                &::before {
                    @include containBg();
                    content: "";
                    display: block;
                    width: 24px;
                    min-width: 24px;
                    height: 24px;
                    margin-right: 4px;
                }

                &--email {
                    &::before {
                        background-image: url(../images/contacts-email.svg);
                    }
                }

                &--phone {
                    &::before {
                        background-image: url(../images/contacts-phone.svg);
                    }
                }

                &--address {
                    &::before {
                        background-image: url(../images/contacts-address.svg);
                    }
                }
            }
        }

        &__socials {
            display: flex;
            flex-wrap: wrap;
            margin-top: 7.4rem;
            @media(max-width: 768px) {
                margin-top: 30px;
            }
            .title {
                width: 100%;
                font-size: 1.8rem;
                line-height: 1.33;
                color: $orange-dim;
                text-transform: uppercase;
            }

            .link {
                margin: 24px 32px 0 0;
                @media(max-width: 768px) {
                    margin: 15px 25px 0 0;
                }
                &:last-child {
                    margin-right: 0;
                }

                svg {
                    width: 100%;
                    height: 100%;
                }

                path {
                    @include transition();
                }

                &:hover {
                    path {
                        fill: $orange-dim;
                    }
                }
            }
        }

        &--items {
            @media(max-width: 992px) {
                display: flex;
                flex-wrap: wrap;
            }
        }
    }

    &-map {
        width: 55.875%;
        padding: 15px;
        background: $white-opacity-15;
        @media (max-width: 1200px) {
            width: 65%;
        }
        @media (max-width: 992px) {
            width: 100%;
            height: 500px;
        }
        @media (max-width: 480px) {
            height: 400px;
        }
    }
}